import './style.less'
import {Button, Card, Divider, Form, Input, PageHeader, Select, Tooltip} from "antd";
import {Content} from "antd/es/layout/layout";
import {useForm} from "antd/es/form/Form";
import {useState} from "react";
import {QuestionCircleOutlined} from "@ant-design/icons";

function App() {
    const [form] = Form.useForm();
    const [dayMoney, setDayMoney] = useState(0);
    const [daySf, setDaySf] = useState(0);
    const [jyF, setJyF] = useState(0);
    const [updateF, setUpdateF] = useState(0);
    const [runF, setRunF] = useState(0);
    const [dayCmoney, setDayCmoney] = useState(0);
    const [initValues, setInitValues] = useState({
        bussinessIndex: undefined,
        bussinessType: 1,
        sales: undefined,
        counter: 10,
        basic: 1800,
        time: 24
    });

    const onFinish = (value) => {
        let sy = 0;
        const {bussinessType, bussinessIndex, sales, counter, basic, time} = value;
        if (bussinessType === 1) {
            if (sales >= 500) {
                const sl = (sales - 100) / 20000 * (bussinessIndex / 100)
                sy = sales * (sl > 0.1 ? 0.1 : sl);
            }
        } else {
            if (sales >= 1500) {
                const sl = (sales - 1000) / 50000 * (bussinessIndex / 100);
                sy = sales * (sl > 0.1 ? 0.1 : sl)
            }
        }
        setJyF(changeCount(sy))
        setUpdateF(5 * (bussinessIndex / 100) * counter)
        setRunF(changeCount(((basic / 10000) * (bussinessIndex / 100) * counter * time)))
        setDayMoney(sales)

    }

    const reset = () => {
        form.resetFields();
    }

    const changeCount = (num) => {
        return Math.floor(num * 100) / 100
    }

    return (
        <div className="App">
            <PageHeader
                className="site-page-header"
                backIcon={false}
                title=""
                subTitle=""
            >
                <div className="panel-content">
                    <Form form={form} initialValues={initValues} style={{marginBottom: 20}} onFinish={onFinish}>
                        <Form.Item label={''} name={'bussinessIndex'}
                                   rules={[{required: true, message: '170%,100%'}]}>
                            <Input placeholder='170' suffix="%" type={'number'}/>
                        </Form.Item>
                        <Form.Item label={''} name={'bussinessType'}>
                            <Select style={{width: '100%'}}>
                                <Select.Option value={1}></Select.Option>
                                <Select.Option value={2}></Select.Option>
                            </Select>
                        </Form.Item>
                        <Form.Item label={''}
                                   name={'sales'}
                                   rules={[{required: true, message: ''}]}>
                            <Input type={'number'} placeholder='' suffix="W"/>
                        </Form.Item>
                        <Form.Item label={''} name={'counter'}>
                            <Input placeholder='' type={'number'} suffix=""/>
                        </Form.Item>
                        <Form.Item label={''} name={'basic'}>
                            <Input placeholder='1800' type={'number'} suffix=""/>
                        </Form.Item>
                        <Form.Item label={''} name={'time'}>
                            <Input placeholder='' disabled suffix=""/>
                        </Form.Item>
                        <div className="btn-group">
                            <Button type={'primary'} htmlType="submit"></Button>
                            <Button onClick={reset}></Button>
                        </div>
                    </Form>
                    <Card bordered>
                        5x100%)x<br/>
                        <Divider dashed/>
                        2000x100%xx<br/>
                        <Divider dashed/>
                        :<br/>
                        <br/>
                        500((-100)20000)100%(0.01%10%)<br/>
                        <br/>
                        1500((-1000)50000)100%(0.01%4%)<br/>

                    </Card>
                </div>
                <Card bordered style={{marginTop: 10}} className={'count-panel'}>
                    <span>{dayMoney}</span>W<br/>
                    <span>{updateF + runF + jyF}</span>W <span>{updateF}</span>W
                    <span>{runF}</span>W <span>{jyF}</span>W<br/>
                    <span>{dayMoney - updateF - runF - jyF}</span>W<br/>
                </Card>
            </PageHeader>
        </div>
    );
}

export default App;
